.loadingSearchBar {
    left: 26%;
    top: 55%;
    font-size: 14px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    letter-spacing: 2.4px;
    text-transform: capitalize;
    position: absolute;
    overflow: hidden;
    transform: translate(-50%, -60%);

}


.loadingSearchBar:before {
    color: #aaa;
    content: attr(data-loading-text);
}

.loadingSearchBar:after {
    top: 0;
    left: 0;
    width: 0;
    opacity: 1;
    color: rgb(139, 0, 49);
    overflow: hidden;
    position: absolute;
    content: attr(data-loading-text);
    animation: loadingSearchBa 5s infinite;
}


@keyframes loadingSearchBa {
    0% {
        width: 0;
    }

    100% {
        width: 100%;
    }
}

