/* Mini SideBar */

.ulStyleSideBar {
    list-style: none;
}

.ulStyleSideBar li {
    padding: 10px;
}

.activeDiv {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F6F7F9;
    border-radius: 5px;

}

.FilterMinibar {
    filter: invert(50%) sepia(97%) saturate(1413%) hue-rotate(0deg) brightness(101%) contrast(106%);

}

.sidebarScroll {
    overflow-y: scroll;
    margin-bottom: 20px;
}

.sidebarScroll::-webkit-scrollbar {
    width: 4px;
    /* width of the entire scrollbar */
}

.sidebarScroll::-webkit-scrollbar-track {
    background: #e7e7e7;
    /* color of the tracking area */
}

.sidebarScroll::-webkit-scrollbar-thumb {
    background-color: orange;
    /* color of the scroll thumb */
    border-radius: 10px;
    /* roundness of the scroll thumb */
    /* creates padding around scroll thumb */
}

.btnSideBarCollapse {
    top: 96px;
